.header{
    display: flex;
    align-items: center;
    justify-content :space-between;
    padding: 20px;
    position:sticky;
    top:0;
    z-index: 100;
    background-color: white;
}
.header__logo{
    height: 25px;
    /* width: 100px; */
    object-fit: contain;
    margin-left: 20px;
}

.header__logo:hover{
    background-color: rgb(241, 239, 239);
    cursor:pointer;
}

.header__left{
    display: flex;
    align-items: center;
}



.header__input{
    display: flex;
    align-items: center;
    width: 40%;
    border: 1px solid lightgray;
}

.header__icons{
    margin-right:10px;
    
}


.header__inputButton{
    width:50px im !important;
    background-color: #fafafa;
    border-left: 1px solid lightgray;
    color: gray;
}

.header__icons{
    display: flex;
    align-items: center;
}
.header__input > input{
    flex: 1px;
    border: none;
}
